@import '/src/common/scss/common.scss';
@import '/src/components/footer/footer.scss';
@import '/src/components/loader/loader.scss';
// components styles
@import '/src/components/logo/logo.scss';
@import '/src/components/nav/nav.scss';
@import '/src/components/header/header.scss';
@import '/src/components/sidebar/sidebar.scss';
@import '/src/components/videos-item/videos-item.scss';
@import '/src/components/section/videos/videos.scss';

.section-1 {
  padding: 25px 0 0px;
}
.settings-box__wrap {
  margin-bottom: 4px;

  background: #d7def9;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
}
.settings-box__wrap--2 {
  display: flex;
}

.settings-box {
  display: flex;
  flex-direction: column;

  padding: 24px 16px;

  .settings-box__wrap--2 & {
    flex: 0 0 50%;
  }
}

.settings-box__title {
  display: flex;
  align-items: center;
  gap: 9px;
  margin-bottom: 42px;

  &svg {
    width: 32px;
    height: 32px;
    flex-shrink: 0;
  }
}

.settings__list {
  display: flex;
  flex-wrap: wrap;
  row-gap: 18px;
  column-gap: 28px;
}
.settings__list--goal {
  max-width: 360px;
}
.settings__list--time {
  max-width: 360px;
}
.settings__list--day {
  gap: 20px;
  margin-bottom: 36px;
}
.settings__label {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 6px;
  width: 160px;
  height: 52px;
  padding: 12px 40px;

  flex-shrink: 0;

  border-radius: 28px;
  background: #fff;
  box-shadow: 0px 4px 4px 0px #abb7ee;
  cursor: pointer;

  & svg {
    fill: #449def;
  }

  &:hover {
    background: #c8d1f9;
    box-shadow: 0px 4px 4px 0px #abb7ee;
  }

  input:checked ~ & {
    color: #ffffff;
    background: #657bdf;
    box-shadow: 0px 4px 4px 0px #ffcb4b;

    & svg {
      fill: #add8ff;
    }
  }
}
.settings__input {
  display: none;
}

.textarea__label {
  display: flex;
  flex-direction: column;
  gap: 4px;
  margin-top: 18px;

  color: #000;
  font-size: 20px;
  line-height: 140%;
}
.textarea {
  width: 100%;
  height: 90px;
  flex-shrink: 0;
  border-radius: 28px;
  background: #fff;
  box-shadow: 0px 4px 4px 0px #abb7ee;
  resize: none;

  border: 1px solid transparent;

  &:hover,
  &:focus {
    // border-radius: 28px;
    border: 1px solid #526484;
  }
}

.contacts-form__list {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 50px;
}
.contacts-form__item {
  display: flex;
  align-items: center;
  gap: 12px;
}
.contacts-form__input-name {
  display: flex;
  align-items: center;
  min-width: 200px;

  color: #000;
  font-size: 24px;
  line-height: 140%;
}
.contacts-form__input {
  width: 624px;
  height: 66px;
  padding: 8px 16px;

  border-radius: 17px;
  background: #fff;
  box-shadow: 0px 4px 4px 0px #abb7ee;
  border: 1px solid transparent;

  &:hover,
  &:focus {
    // border-radius: 28px;
    border: 1px solid #526484;
  }

  &[type="file"] {
    cursor: pointer;
  }
}
.contacts-form__select {
  @extend .contacts-form__input;
  cursor: pointer;

  display: flex;
  padding: 19px 38px 19px 39px;
  align-items: center;
  justify-content: space-between;
  width: 190px;

  color: #000;
  font-size: 20px;
  line-height: 140%;

  -moz-appearance: none; /* Firefox */
  -webkit-appearance: none; /* Safari and Chrome */
  appearance: none;
}
.contacts-form__select option {
  color: #000;
  font-family: Inter;
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 140%;
}

.contacts-form__input-file {
  display: none;

  &[type='file' i] {
    background: green;
  }
}
.contacts-form__label-file {
  position: relative;
  @extend .contacts-form__input;
  display: flex;
  padding: 19px 38px 19px 39px;
  justify-content: center;
  align-items: center;
  width: 190px;
  height: 66px;

  color: #000;
  font-size: 20px;
  line-height: 140%;
  cursor: pointer;
}

.contacts-form__label-select {
  position: relative;

  & svg {
    position: absolute;
    right: 19px;
    top: 50%;
    transform: translateY(-50%);
  }
}

.contacts-form__input-file-name {
  position: absolute;
  top: 0;
  left: calc(100% + 32px);

  display: flex;
  padding: 19px 20px;
  justify-content: center;
  align-items: center;
  min-width: 188px;
  height: 66px;

  color: #fff;
  font-weight: 400;
  font-size: 20px;
  line-height: 140%; /* 28px */

  border-radius: 17px;
  background: #526484;
  box-shadow: 0px 4px 4px 0px #abb7ee;
}

.section-2 {
  padding: 4px 0 100px;
}
.title {
  display: flex;
  align-items: center;
  gap: 8px;

  color: #000;
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  line-height: 140%;
}

.btn__wrap {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 42px;
  margin-top: 48px;
}

.btn--propose {
  display: flex;
  padding: 14px 120px 13px 120px;
  justify-content: center;
  align-items: center;
  height: 72px;
  width: 390px;

  color: #FFF;
font-size: 32px;
line-height: 140%; 

  border-radius: 28px;
  background: #657bdf;
  box-shadow: 0px 4px 4px 0px #ffcb4b;

  &:hover {
    color: #464646;
    background: #c8d1f9;
    box-shadow: 0px 4px 4px 0px #abb7ee;
  }
}

.btn--cancel {
  display: flex;
  width: 390px;
  height: 72px;
  padding: 14px 0px 13px 0px;
  justify-content: center;
  align-items: center;

  color: #464646;
font-size: 32px;
line-height: 140%; 

  border-radius: 28px;
  background: #fff;

  &:hover {
    color: #464646;
    background: #c8d1f9;
    box-shadow: 0px 4px 4px 0px #abb7ee;
  }
}
