.header {
  @extend %flex-ac;
  width: 100%;
  height: 120px;
  padding: 49px 0 25px;

  background: #E6E9F9;
  border-bottom: 1px solid #E0DEDE;

  box-shadow: 0px 4px 4px 0px #ABB7EE;
}
.header__container {
  @extend %flex-ac;
  gap: 12px;
  width: 100%;
}

.main-title {
  color: #000;
  font-size: 32px;
  font-weight: 700;
  line-height: 140%;
}
