.sidebar {
  flex: 0 0 360px;
  display: flex;
}
.sidebar__main-nav {
  flex: 0 0 72px;
  padding: 12px 8px;

  background: linear-gradient(90deg, #7D2AF3 0%, #6710F2 100%);
}
.sidebar__nav {
  flex: 0 0 288px;
  background: #E4DEF3;
  border-right: 1px solid #A9AEB8;
}

.main-nav__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 24px;
}
.main-nav__link {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 56px;
  height: 56px;

  color: #FFF;

  // font-family: Inter;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 140%; /* 16.8px */
  letter-spacing: 0.72px;

  border-radius: 8px;
  // border: 1px solid transparent;

  &.current { background: linear-gradient(132deg, #6609F5 0.05%, rgba(71, 17, 155, 0.99) 105.68%);}
  &:hover {border: 1px solid #FFF;}
}
.main-nav__link svg {
  flex-shrink: 0;
  width: 24px;
  height: 24px;
}

.chat__box {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  height: 64px;

  background: #EBECFF;
}
.chat__btn-back {
  flex: 0 0 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 8px;
  width: 32px;
  height: 32px;

  background-color: transparent;
}
.chat__img {
  flex-shrink: 0;
  width: 40px;
  height: 40px;

  border-radius: 8px;
}
.chat__title {
  color: #526484;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 140%; /* 22.4px */
  letter-spacing: 0.15px;
}
.chat__info {
  color: #91969E;
  font-size: 13px;
  font-style: normal;
  font-weight: 400;
  line-height: 140%; /* 18.2px */
  letter-spacing: 0.15px;
}

.nav__list {
  display: flex;
  flex-direction: column;
  gap: 4px;
  padding: 0 16px;
}
.nav__link {
  display: flex;
  align-items: center;
  gap: 8px;
  height: 44px;
  padding: 10px 8px;

  color: #526484;
  font-size: 15px;
  font-weight: 600;
  line-height: 140%; /* 21px */
  letter-spacing: 0.15px;

  border-radius: 12px;
  border: 1px solid transparent;

  &.current { background: #D0C2F3; }
  &:hover {border: 1px solid #D0C2F3;}
}
.nav__link svg {
  width: 16px;
  height: 16px;
  flex-shrink: 0;
}
.nav__link span {
  display: flex;
  width: 24px;
  height: 24px;
  padding: 4px 8px;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  margin: 0 16px 0 auto;

  color: #FFF;
  font-size: 12px;
  font-weight: 500;
  line-height: 20px; /* 166.667% */
  letter-spacing: 0.15px;

  border-radius: 158px;
  background: #7D2AF3;

  &.isHidden { display: none;}
}